import styled from 'styled-components'

export const Wrapper = styled.div`
  background: rgb(250, 250, 250);
  transform: skewY(-3deg);
  padding: 50px 0;
`

export const CardWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  /* grid-row-gap: 50px; */
  /* grid-column-gap: 50px; */

  padding-top: 30px;

  @media screen and (min-width: 1201px) {
    /* grid-template-columns: repeat(3, 1fr); */
  };

  transform: skewY(3deg);
`

export const Card = styled.div`
  background-color: white;

  max-width: 85%;
  /* width: 350px;
  height: 400px; */

  width: ${props => props.bestOption ? 350 * 1.1 : 350}px;
  height: ${props => props.bestOption ? 400 * 1.1 : 400}px;
  
  margin-left: 40px;
  margin-right: 40px;
  margin-bottom: 50px;

  box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.15),
              0px 0px 1px rgba(0, 0, 0, 0.15);
  
  position: relative;

  border-top: ${props => props.bestOption ? '5px solid rgb(40, 120, 190)' : null};
`

export const Header = styled.h2`
  text-align: center;
`

export const List = styled.p`
  text-align: center;
  list-style: none;
`

export const Button = styled.button`
  position: absolute;
  /* bottom: 20px; */
  bottom: ${props => props.bestOption ? 30 : 20}px;
  left: 50%;
  transform: translateX(-50%);
  border: none;
  outline: none;
  font-size: 1.25em;
  padding: 1em 1.75em 1em 1.75em;
  cursor: pointer;
  background-color: #08cc15;
  color: white;

  transition: background-color 0.05s ease-out;

  &:hover {
    background-color: #01b20d;
  };
`