import React from 'react'
import { ThemePovider } from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import {
	Wrapper,
  CardWrapper,
  Card,
  Header,
  List,
  Button
} from './style'

const Check = () => {
  return <FontAwesomeIcon icon={['far', 'check']} style={{color: 'green'}}/>
}

const PricingSection = () => {
  return (
    <Wrapper>
      <CardWrapper>
        <Card>
          <Header>Free Plan</Header>
          <List><Check /> Feature 1</List>
          <List><Check /> Feature 2</List>
          <Button>Free</Button>
        </Card>
        <Card>
          <Header>Premium Plan</Header>
          <List><Check /> Feature 1</List>
          <List><Check /> Feature 2</List>
          <List><Check /> Feature 3</List>
          <List><Check /> Feature 4</List>
          <Button>$3 / Month</Button>
        </Card>
        <Card bestOption>
          <Header>Full Option Plan</Header>
          <List><Check /> Feature 1</List>
          <List><Check /> Feature 2</List>
          <List><Check /> Feature 3</List>
          <List><Check /> Feature 4</List>
          <List><Check /> Feature 5</List>
          <List><Check /> Feature 6</List>
          <Button bestOption>$6 / Month</Button>
        </Card>
      </CardWrapper>
    </Wrapper>
  )
}

export default PricingSection