import React from 'react'

import Navbar from './components/Navbar/index'

import {
	Wrapper
} from './styles/wrapper'

import {
	PlaySection,
	MainText,
	PlayNowWrapper,
	PlayNowButton
} from './styles/playSection'

import PricingSection from './components/PricingSection/index'
import FeatureSection from './components/FeatureSection/index'

// import {
// 	AboutSection
// } from './styles/aboutSection'

const LandingPage = () => {
	return (
		<Wrapper>
			<Navbar />
			<PlaySection>
				<PlayNowWrapper>
					<MainText>
						Play and improve your chess
					</MainText>
					<PlayNowButton>Play Now</PlayNowButton>
				</PlayNowWrapper>
			</PlaySection>
			<FeatureSection />
			<PricingSection />
			{/* <AboutSection></AboutSection> */}
		</Wrapper>
	)
}

export default LandingPage