import React, { useState } from 'react'
import { ThemeProvider } from 'styled-components'

import {
  OuterWrapper,
  InnerWrapper,
  Brand,
  MobileNavs,
  MobileNav,
  DesktopNavs,
  DesktopNav
} from './style'

import './hamburger.css'

import useScreen from '../../../../hooks/useScreen'

const Navbar = props => {
  const [navClicked, setNavClicked] = useState(false)
  const { width, scrollY } = useScreen()

  const { mobileWidth, opaqueScroll } = props
  const isMobile = width <= mobileWidth
  const isOpaqueNav = scrollY >= opaqueScroll
  const isActive = navClicked ? 'is-active' : ''

  const togleNavClicked = () => {
    setNavClicked(!navClicked)
  }

  const renderMobileNavbar = () => {
    if (!isMobile)
      return null
    return (
      <>
        <InnerWrapper>
          <Brand>CHESSZI</Brand>
          <button
            className={`hamburger hamburger--spin ${isActive}`}
            type="button"
            onClick={togleNavClicked}
          >
            <span className="hamburger-box">
              <span className="hamburger-inner"></span>
            </span>
          </button>
        </InnerWrapper>
        <MobileNavs>
          <MobileNav>Play</MobileNav>
          <MobileNav>Features</MobileNav>
          <MobileNav>Pricing</MobileNav>
          <MobileNav>About</MobileNav>
        </MobileNavs>
      </>
    )
  }

  const renderDesktopNavbar = () => {
    if (isMobile)
      return null
    return (
      <InnerWrapper>
        <Brand>CHESSZI</Brand>
        <DesktopNavs>
          {/* <DesktopNav onClick={() => scroll.scrollTo('feature', {smooth: true, duration: 100})} >Play</DesktopNav> */}
          <DesktopNav>Play</DesktopNav>
          <DesktopNav>Features</DesktopNav>
          <DesktopNav>Pricing</DesktopNav>
          <DesktopNav>About</DesktopNav>
        </DesktopNavs>
      </InnerWrapper>
    )
  }

  return (
    <ThemeProvider theme={{ navClicked, isOpaqueNav }}>
      <OuterWrapper>
        {renderMobileNavbar()}
        {renderDesktopNavbar()}
      </OuterWrapper>
    </ThemeProvider>
  )
}

Navbar.defaultProps = {
  mobileWidth: 700,
  opaqueScroll: 100
}

export default Navbar