import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import {
  Wrapper,
  Feature,
  Icon,
  Detail,
  Header,
  Text
} from './style'

const features = [
  {
    icon: ['far', 'chess-king'],
    header: 'Feature 1',
    text: 'Veniam laborum incididunt quis ex adipisicing excepteur anim culpa consectetur sit ex.'
  },
  {
    icon: ['far', 'chess-queen'],
    header: 'Feature 2',
    text: 'Veniam laborum incididunt quis ex adipisicing excepteur anim culpa consectetur sit ex.'
  },
  {
    icon: ['far', 'chess-rook'],
    header: 'Feature 3',
    text: 'Veniam laborum incididunt quis ex adipisicing excepteur anim culpa consectetur sit ex.'
  },
  {
    icon: ['far', 'chess-knight'],
    header: 'Feature 4',
    text: 'Veniam laborum incididunt quis ex adipisicing excepteur anim culpa consectetur sit ex.'
  },
  {
    icon: ['far', 'chess-bishop'],
    header: 'Feature 5',
    text: 'Veniam laborum incididunt quis ex adipisicing excepteur anim culpa consectetur sit ex.'
  },
  {
    icon: ['far', 'chess-pawn'],
    header: 'Feature 6',
    text: 'Veniam laborum incididunt quis ex adipisicing excepteur anim culpa consectetur sit ex.'
  },
]

const FeatureSection = () => {
  const renderFeatures = features => {
    return features.map((feature, i) => {
      const { icon, header, text } = feature
      return (
        <Feature key={`${header}-${i}`}>
          <Icon>
            <FontAwesomeIcon icon={icon} />
          </Icon>
          <Detail>
            <Header>{header}</Header>
            <Text>{text}</Text>
          </Detail>
        </Feature>
      )
    })
  }

  return (
    <Wrapper>
      {renderFeatures(features)}
    </Wrapper>
  )
}

export default FeatureSection