import styled from 'styled-components'

const normalHeight = 100
const opaqueHeight = 60

export const OuterWrapper = styled.div`
  background: ${props => {
    if (props.theme.isOpaqueNav)
      return `black`
    return `
      linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.9),
        rgba(0, 0, 0, 0)
      )
    `
  }};
  height: ${props => props.theme.isOpaqueNav ? opaqueHeight : normalHeight}px;
  position: fixed;
  width: 100%;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  letter-spacing: 0.035em;
  user-select: none;

  transition: height 0.15s ease-out;
`

export const InnerWrapper = styled.div`
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: inherit;
`

export const Brand = styled.div`
  cursor: pointer;
  font-size: ${props => props.theme.isOpaqueNav ? '1.5em' : '1.75em'};
  transition: font-size 0.15s ease-out;
  letter-spacing: 0.07em;
`

export const MobileNavs = styled.div`
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: black;
  font-size: 1.25em;
  
  transition: clip-path 0.2s ease-out,
              padding-top 0.15s ease-out;

  padding-top: ${props => props.theme.isOpaqueNav ? `${opaqueHeight}px` : `${normalHeight}px`};
  padding-bottom: 1.5em;

  clip-path: ${props => {
    const clipBottom = props.theme.navClicked ? '0%' : '100%'
    return `inset(0 0 ${clipBottom} 0)`
  }};
`

export const MobileNav = styled.div`
  cursor: pointer;
  padding: 1em 0;
`

export const DesktopNavs = styled.div`
  width: 100%;
  display: flex;
  font-size: 1.25em;
  justify-content: flex-end;
`

export const DesktopNav = styled.div`
  cursor: pointer;
  margin-left: 7vw;

  &:first-child {
    margin-left: 0;
  }
`