import styled from 'styled-components'

import backgroundImage from './images/chessboard.jpg'

const height = 500;

export const PlaySection = styled.div`
  background-image: linear-gradient(rgba(0, 0, 0, 0.325), rgba(0, 0, 0, 0.325)), url(${backgroundImage});
  /* opacity: 0.5; */
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;

  /* height: 100vh; */
  height: ${height}px;
`

export const PlayNowWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  /* height: 100vh; */
  height: ${height}px;
`

export const MainText = styled.div`
  margin-bottom: 50px;
  color: white;
  font-size: 1.8em;
  text-align: center;
  text-shadow: 5px 5px 20px rgba(0, 0, 0, 0.4);
  padding: 0 0.75em;

  @media screen and (min-width: 701px) {
    font-size: 2.25em;
  }
`

export const PlayNowButton = styled.button`
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: uppercase;
  cursor: pointer;

  margin-bottom: 80px;

  font-size: 1.5em;
  padding: 1.1em 1.7em 1.1em 1.7em;
  border-radius: 1000px;

  background-color: rgb(40, 120, 190);
  color: white;

  box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.4);

  transition: background-color 0.05s ease-out;

  &:hover {
    background-color: rgb(50, 105, 170);
  }
`